// m t cu hi
import { Component } from '@angular/core';
import { CountQuestion } from "../count-question";

@Component({
    selector: 'dem10',
    styleUrls: ['../../css/dem10.css'],
    template: `
<section>
    <h1>Bi 1 - Chng 1: m trong phm vi 10</h1>
    <h2 class="type" >in s thch hp vo  trng.</h2>
    <div class="container">
        <div class="row">
            <!-- ct hin th cu hi -->
            <div class="col-sm-1"></div>
            <div class="col-sm-6 col-xs-12">
                <!-- cu hi hin trong panel -->
                <div class="panel panel-body panel-primary" id="divQues">
                    <div *ngIf="showAns">
                        <!-- hin th cu hi -->
                        <h3><strong>{{QUESTIONES[count].name_ques}}</strong></h3>
                        <div id="getImgQues"><img src={{QUESTIONES[count].link_img}} /></div>
                        <div id="ans" >
                            <input type="text" style="text-align: center" #result id="ketqua">
                        </div>
                        <div id="submit_ans">
                            <button type="button" (click)="checkAns(result.value); result.value=''" id="kiemtra">Kim tra</button>
                        </div>
                    </div>
                    <!-- chuyn sang bi tip theo -->
                    <div *ngIf="nextLess">
                        <div id="newLess">
                            <!-- hon thnh c 10 cu hi ng -->
                            <div *ngIf="endPer">
                                <h2><strong>Chc mng bn  hon thnh xut sc bi hc 1!</strong></h2>
                                <h2>Bn  t c 10/10 sao.</h2>
                                <h2><strong>Chuyn n bi hc mi no</strong></h2>
                            </div>
                            <div *ngIf="endLess">
                                <h2><strong>Chc mng bn  hon thnh bi hc 1!</strong></h2>
                                <h2>Bn  t c {{count_true}}/10 sao.</h2>
                                <h2><strong>Chuyn n bi hc mi no</strong></h2>
                            </div>
                            <h2><a routerLink="/sub10" routerLinkActive="active">Bi 2 - chng 1: Php tr trong phm vi 10. </a></h2>
                            <router-outlet></router-outlet>
                        </div>
                    </div>
                </div>
            </div>
            <!-- ct hin th s sao -->
            <div class="col-sm-4">
                <!-- s sao hin trong panel -->
                <h2 class="type1"><strong>S sao t c </strong></h2>
                <br>
                <div class="panel panel-body panel-primary" id="panel_star">
                    <div id="starAward">
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
  `,
})

export class dem10 {
    QUESTIONES: CountQuestion[] = [{
        name_ques: "C bao nhiu con voi trn hnh?",
        link_img: "./assets/image/1voi.PNG",
        true_ans: 1
     }, {
        name_ques: "C bao nhiu con mui ? ",
        link_img: "./assets/image/6muoi.PNG",
        true_ans: 6
    }, {
        name_ques: "C bao nhiu cu vng? ",
        link_img: "./assets/image/2cauVong.PNG",
        true_ans: 2
    }, {
        name_ques: "C bao nhiu qu bng trn hnh?",
        link_img: "./assets/image/3bong.PNG",
        true_ans: 3
    }, {
        name_ques: "C bao nhiu mn qu ? ",
        link_img: "./assets/image/7qua.PNG",
        true_ans: 7
    }, {
        name_ques: "C bao nhiu ngi? ",
        link_img: "./assets/image/9nguoi.PNG",
        true_ans: 9
    }, {
        name_ques: "C bao nhiu con vt?",
        link_img: "./assets/image/5vit.PNG",
        true_ans: 5
    }, {
        name_ques: "C bao nhiu con th ?",
        link_img: "./assets/image/3nam.jpg",
        true_ans: 4
    },  {
        name_ques: "C bao nhiu ci chn?",
        link_img: "./assets/image/10chen.PNG",
        true_ans: 10
        
    }, {
        name_ques: "C bao nhiu con nga trong hnh?",
        link_img: "./assets/image/8ngua.PNG",
        true_ans: 8
        
    }];

    showAns = true; // hin cu hi 
    nextLess = false;
    count = 0; //s cu hi
    count_true = 0; //s cu tr li ng
    endPer = false; //tr li ng ht tt c cc cu hi
    endLess = false; 

    rightAns(): void {      // tr li ng thm mt sao vo khi c id = starAward
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/true.png");
        document.getElementById("starAward").appendChild(img);
    }

    checkAns(value) {
        if (parseInt(value) == this.QUESTIONES[this.count].true_ans) {
            this.rightAns();          // thm 1 ngi sao vo trong khung 
            this.count_true += 1;     // s cu ng tng thm 1
            if (this.count <= this.QUESTIONES.length)
                this.count += 1;            // ch s i tng tng ln 1
            if (this.count_true == this.QUESTIONES.length ) {  // ch s bng  di, s cu ng =  di                
                this.showAns = false;
                this.nextLess = true;
                this.endPer = true;
            }
        } else { 
            if (this.count <= this.QUESTIONES.length)
                this.count += 1;
        }
        if (this.count == this.QUESTIONES.length && this.endPer == false) {                
            this.showAns = false;
            this.nextLess = true;
            this.endLess = true;
            if (this.count_true == 0) {
                var para = document.createElement("P");
                var t = document.createTextNode("Bn khng t c sao no, c gng ln sau nh!");
                para.appendChild(t);
                document.getElementById("panel_star").appendChild(para);
            }
        }
    }
}


