import { Component } from '@angular/core';

@Component({
    selector: 'luyen-tap',
    styleUrls: ['../css/luyentap.css'],
    template: `
   <div class="container">
        <div class="row">
            <h1 style="text-align: center; font-size: 50px; color: brown">Ton 1</h1>
            <div class="col-xs-10 col-sm-6">
                <div class="panel panel-body panel-primary" id="c1">
                    <h2> Cc s v php ton trong phm vi 10</h2>
                    <a routerLink="/dem10" routerLinkActive="active">m n 10</a>
                    <br>
                    <a routerLink="/sub10" routerLinkActive="active">Php tr</a>
                        <br>
                        <a routerLink="/sum10" routerLinkActive="active">Php cng</a>
                        <br>
                        <a routerLink="/minmax10" routerLinkActive="active">Tm s ln nht, nh nht</a>
                    </div>
                </div>
            <div class="col-xs-10 col-sm-6">
                <div class="panel panel-body panel-primary" id="c2">
                    <h2>Cc s v php ton trong phm vi 20</h2>
                    <a routerLink="/sosanh20" routerLinkActive="active">So snh 2</a>
                    <br>
                    <a routerLink="/sub20" routerLinkActive="active">Php tr</a>
                    <br>
                    <a routerLink="/sum20" routerLinkActive="active">Php cng</a>
                    <br>
                    <a routerLink="/minmax20" routerLinkActive="active">Tm s ln nht, nh nht</a>
                    <br>
                    <a routerLink="/lientruocsau20" routerLinkActive="active">Tm s lin trc, s lin sau</a>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-xs-10 col-sm-6">
                <div class="panel panel-body panel-primary" id="c3">
                    <h2>Cc s v php ton trong phm vi 100</h2>
                    <a routerLink="/sosanh100" routerLinkActive="active">So snh 2</a>
                    <br>
                    <a routerLink="/sub100" routerLinkActive="active">Php tr</a>
                    <br>
                    <a routerLink="/sum100" routerLinkActive="active">Php cng</a>
                    <br>
                    <a routerLink="/minmax100" routerLinkActive="active">Tm s ln nht, nh nht</a>
                    <br>
                    <a routerLink="/lientruocsau100" routerLinkActive="active">Tm s lin trc, s lin sau</a>
                    <br>
                </div>
            </div>
            <div class="col-xs-10 col-sm-6">
                <div class="panel panel-body panel-primary" id="c4">
                    <h2>Hnh hc</h2>
                    <a routerLink="/diemdoanthang" routerLinkActive="active">im v on thng</a>
                    <br>
                    <a routerLink="/tamgiacvuongtron" routerLinkActive="active">Hnh tam gic, hnh vung, hnh trn</a>
                    <br>  
                    <a routerLink="/demtamgiac" routerLinkActive="active">m s hnh tam gic</a>
                    <br>    
                </div>             
            </div>
            <div class="col-xs-10 col-sm-6">
                <div class="panel panel-body panel-primary" id="c4">
                    <h2>Thi gian</h2>
                    <a routerLink="/xemgio" routerLinkActive="active">Xem gi</a>
                    <br>
                </div>
            </div>
        </div>
        <router-outlet></router-outlet>
    </div>
    `,
})

export class luyenTap{}


