import { Component } from '@angular/core';
import { CountQuestion } from 'app/luyentap/count-question';
@Component({
    selector: 'diem-doanthang',
    styleUrls:['../../css/chuong4.css'],
    templateUrl: 'chuong4.html',
})

export class demTamgiac {
    countques: CountQuestion[] = [
        {
            name_ques: "m s hnh tam gic.",
            link_img: "./assets/image/tamgiac1.jpg",
            true_ans: 4
        },
        {
            name_ques: "m s hnh tam gic.",
            link_img: "./assets/image/tamgiac.jpg",
            true_ans: 6
        },
        {
            name_ques: "m s hnh tam gic.",
            link_img: "./assets/image/tamgiac3.jpg",
            true_ans: 4
        },
        {
            name_ques: "m s hnh tam gic.",
            link_img: "./assets/image/tamgiac4.jpg",
            true_ans: 5
        },
        {
            name_ques: "m s hnh tam gic.",
            link_img: "./assets/image/tamgiac2.jpg",
            true_ans: 5
        }
    ];

    b1_c4_head = false;
    b2_c4_head = true;
    nameQues = true;
    showAns = true; // hin cu hi 
    nextLess = false;
    count = 0;
    count_true = 0;
    endPer = false;
    endLess = false;
    bai2_chuong4= false;
    bai1_chuong5 = false;

    rightAns(): void {      // tr li ng thm mt sao vo khi c id = starAward
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/true.png");
        document.getElementById("starAward").appendChild(img);
    }

    checkAns(value) {
        if (parseInt(value) == this.countques[this.count].true_ans) {
            this.rightAns();          // thm 1 ngi sao vo trong khung 
            this.count_true += 1;     // s cu ng tng thm 1
            if (this.count <= this.countques.length)
                this.count += 1;            // ch s i tng tng ln 1
            if (this.count_true == this.countques.length) {  // ch s bng  di, s cu ng =  di
                this.nameQues = false;
                this.showAns = false;
                this.nextLess = true;
                this.endPer = true;
                this.bai1_chuong5 = true;
            }
        } else {
            if (this.count <= this.countques.length)
                this.count += 1;
        }
        if (this.count == this.countques.length && this.endPer == false) {
            this.nameQues = false;
            this.showAns = false;
            this.nextLess = true;
            this.endLess = true;
            this.bai1_chuong5 = true;
            if (this.count_true == 0) {
                var para = document.createElement("P");
                var t = document.createTextNode("Bn khng t c sao no, c gng ln sau nh!");
                para.appendChild(t);
                document.getElementById("panel_star").appendChild(para);
            }
        }
    }
}


