import { Component } from '@angular/core';
import { test } from './test';

@Component({
    selector: 'de5',
    templateUrl: 'de.html',
    styleUrls: ['../css/de.css'],

})

export class de5 {
    tests: test[] = [
        {
            name_que: "C bao nhiu con th trong hnh bn di",
            name_link: "./assets/image/tho8.jpg",
            conten_ques: "",
            input_ans: "",
            true_ans: 8
        },
        {
            name_que: "Thc hin php tnh",
            name_link: "",
            conten_ques: "20 + 20 = ??? ",
            input_ans: "",
            true_ans: 40
        },
        {
            name_que: "m s on thng xut hin trn hnh",
            name_link: "./assets/image/doanthang.jpg",
            conten_ques: "",
            input_ans: "",
            true_ans: 7
        },
        {
            name_que: "S ln nht trong cc s sau ?",
            name_link: "",
            conten_ques: "56, 45, 34, 78, 49, 23",
            input_ans: "",
            true_ans: 78
        },
        {
            name_que: "m s im nm bn trong hnh trn",
            name_link: "./assets/image/diem1.jpg",
            conten_ques: "",
            input_ans: "",
            true_ans: 6
        },
        {
            name_que: "Ngy 29/1/2005 l vo th my trong tun? (in s vo  di)",
            name_link: "./assets/image/lich2.png",
            conten_ques: "",
            input_ans: "",
            true_ans: 7
        },
        {
            name_que: "Thc hin php tnh sau.",
            name_link: "",
            conten_ques: "40 - 20 = ???",
            input_ans: "",
            true_ans: 20
        },
        {
            name_que: "S ln nht trong dy s sau",
            name_link: "",
            conten_ques: "34, 56, 70, 29, 78",
            input_ans: "",
            true_ans: 78
        },
        {
            name_que: "in s thch hp",
            name_link: "",
            conten_ques: "18 dm + 20 dm = ???",
            input_ans: "",
            true_ans: 38
        },
         {
            name_que: "Trang hi c 23 bng hoa. Phng hi c 18 bng hoa. Hi c 2 bn hi c tt c bao nhiu bng hoa",
            name_link: "",
            conten_ques: "23 bng hoa + 18 bng hoa = ???",
            input_ans: "",
            true_ans: 41
        }

    ];
    showGuide = true;
    showChoice = false;
    showChoiceCheck = false;
    first = true;

    imgTrue(id) {
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/dung.png");
        document.getElementById(id).appendChild(img);
    }
    imgFalse(id) {
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/sai.png");
        document.getElementById(id).appendChild(img);
    }

    cauhoi() {
        this.showGuide = false;
        for (var i = 0; i < this.tests.length; i++) {
            var d1 = document.createElement("DIV");
            d1.setAttribute("class", "panel panel-body panel-primary");
            d1.setAttribute("id", "q" + i);
            document.getElementById("divQues").appendChild(d1); // thm panel vo col
            // to tn cu hi h3
            var h3 = document.createElement("H3");
            var txt1 = document.createTextNode(this.tests[i].name_que);
            h3.appendChild(txt1);
            document.getElementById("q" + i).appendChild(h3); // thm h3 vo panel
            // to hnh nh
            var img = document.createElement("IMG");
            img.setAttribute("src", this.tests[i].name_link);
            document.getElementById("q" + i).appendChild(img);    // thm img vo panel
            //to ni dung nu l tnh ton h2
            var h2 = document.createElement("H2");
            var txt2 = document.createTextNode(this.tests[i].conten_ques);
            h2.appendChild(txt2);
            document.getElementById("q" + i).appendChild(h2);     // thm h2 vo panel
            // to input h2, input
            var ip = document.createElement("INPUT");
            ip.setAttribute("type", "text");
            ip.setAttribute("id", "ip" + i);
            ip.setAttribute("style", "text-align: center");
            document.getElementById("q" + i).appendChild(ip);
        }
        this.showChoice = true;
        this.showChoiceCheck = true;
    }

    checkAns() {
        this.first = false;
        this.showChoiceCheck = false;
        var j;
        var diem = 0 ; 
        for (j = 0; j < this.tests.length; j++) {
            var txtkq = (<HTMLInputElement>document.getElementById("ip" + j)).value;
            if (parseInt(txtkq) == this.tests[j].true_ans) {
                this.imgTrue("q"+j);
                diem = diem + 1;
            }
            else {
                this
                .imgFalse("q"+j);
                diem = diem + 0;
            }
        }
        typeof diem;
        var d1 = diem.toString();
        var txt2 = document.createTextNode(d1);
        document.getElementById("scoreValue").appendChild(txt2);
    }
}


