import { Component } from '@angular/core';
import { Clock } from './clock';

@Component({
    selector: 'xem-gio',
    styleUrls:['../../css/clock.css'],
    templateUrl: 'xem-gio.html',
})

export class xemGio {
    clocks: Clock[] = [
        {
            link_img: "./assets/image/clock4.png",
            ans_A: "10 gi 10 pht",
            ans_B: "2 gi 50 pht",
            ans_C: "3 gi km 10 pht",
            true_ans: "10 gi 10 pht",
        },
        {
            link_img: "./assets/image/clock2.png",
            ans_A: "12 gi 15 pht",
            ans_B: "3 gi",
            ans_C: "12 gi",
            true_ans: "3 gi",
        },
        {
            link_img: "./assets/image/clock3.png",
            ans_A: "11 gi",
            ans_B: "11 gi 55 pht",
            ans_C: "12 gi 55 pht",
            true_ans: "11 gi 55 pht",
        },
        {
            link_img: "./assets/image/clock.png",
            ans_A: "12 gi 40 pht",
            ans_B: "11 gi 40 pht",
            ans_C: "8 gi",
            true_ans: "8 gi",
        },
        {
            link_img: "./assets/image/clock1.jpg",
            ans_A: "1 gi 45 pht",
            ans_B: "9 gi 5 pht",
            ans_C: "1 gi km 15 pht",
            true_ans: "1 gi km 15 pht",
        }
    ];

    count = 0;
    showAns = true;
    count_true = 0;
    nextLess = false;
    theEnd = false;

    imgTrue(id) {
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/dung.png");
        document.getElementById(id).appendChild(img);
    }

    imgFalse(id) {
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/sai.png");
        document.getElementById(id).appendChild(img);
    }

    checkAns(value) {
        if (value == this.clocks[this.count].true_ans) {
            this.count_true += 1;
            this.imgTrue(this.count);
            if (this.count < this.clocks.length) this.count += 1;
            if (this.count_true == 5) {
                this.showAns = false;
                this.theEnd = true;
            }
        } else {
            this.imgFalse(this.count);
            if (this.count < this.clocks.length) this.count += 1;
        }
        if (this.count == 5) {
            this.showAns = false;
            this.nextLess = true;
            this.theEnd = true;
        }
    }
}



