import { Component } from '@angular/core';

@Component({
    selector: 'lien-truoc-sau-20',
    styleUrls:['../../css/lientruoc-sau.css'],
    templateUrl: 'lientruoc-sau-20.html',
})

export class lienTruocSau20 {
    _num = Math.floor(Math.random() * 19 + 1);
    _nameQues = "Tm s lin trc ca: ";
    _nameAns = 'S lin trc l : ';
    showAns = true;
    nextLess = false;
    endPer = false;
    endLess = false;
    count = 0;
    count_true = 0;

    init() {
        var _signal = Math.floor(Math.random() * 2 + 1);
        if (_signal == 1) {
            this._nameQues = "Tm s lin trc ca: ";
            this._nameAns = "S lin trc l: ";
        } else {
            this._nameQues = "Tm s lin sau ca: ";
            this._nameAns = "S lin sau l: ";
        }
        this._num = Math.floor(Math.random() * 19 + 1);
    }

    rightAns(): void {      // tr li ng thm mt sao vo khi c id = starAward
        var img = document.createElement("IMG");
        img.setAttribute("src", "./assets/image/true.png");
        document.getElementById("starAward").appendChild(img);
    }

    checkAns(value) {
        if (this._nameQues == "Tm s lin trc ca: ") {
            if (value == '') {
                alert("Bn phi in gi tr vo  trng!");
            }
            else if (parseInt(value) == (this._num - 1)) {
                this.rightAns();
                this.count += 1;
                this.count_true += 1;
                this.init();
                if (this.count_true == 10) {
                    this.showAns = false;
                    this.nextLess = true;
                    this.endPer = true;
                }
            }
            else {
                this.count += 1;
                this.init();
            }
            if (this.count == 10 && this.endPer == false) {
                this.showAns = false;
                this.nextLess = true;
                this.endLess = true;
            }
        } else {
            if (parseInt(value) == (this._num + 1)) {
                this.rightAns();
                this.count += 1;
                this.count_true += 1;
                this.init();
                if (this.count_true == 10) {
                    this.showAns = false;
                    this.nextLess = true;
                    this.endPer = true;
                }
            }
            else {
                this.count += 1;
                this.init();                
            }
            if (this.count == 10 && this.endPer == false) {
                this.showAns = false;
                this.nextLess = true;
                this.endLess = true;
            }
        }
    }
}


